<template>
  <div id="textbox">
      <div :class="editable ? 'hold' : ''" class="pt-2 pb-1">
        <div v-if="editable" class="px-3">
          <v-text-field
            class="my-5 font-weight-bold"
            v-model="data.Text"
            v-on:keyup.enter="editable = !editable"
            dense
            autofocus
          ></v-text-field>
          <v-text-field
            label="Ch m"
            class="my-2"
            v-model="data.Holder"
            v-on:keyup.enter="editable = !editable"
            dense
          />
          <v-flex class="mx-3">
            <v-row>
              <v-col style="height: 40px" class="drag">
                <v-switch
                  v-model="data.Required"
                  class="my-0 py-0"
                  label="Bt buc"
                ></v-switch>
              </v-col>
              <v-spacer></v-spacer>
              <toolbax
                :change="change"
                :remove="remove"
                @editable="editing(false);editable = $event"
              ></toolbax>
            </v-row>
          </v-flex>
        </div>
        <div class="pointer" v-if="!editable" @click="editable = !editable; editing(true)">
          <div class="mb-3 font-weight-bold" v-text="data.Text"></div>
          <v-text-field
            disabled
            hide-details="false"
            class="mx-0"
            :placeholder="data.Holder"
            outlined
            dense
          />
        </div>
      </div>
  </div>
</template>

<script>
import toolbax from "./layout/bottom-toolbar";
export default {
  name: "Textbox",
  props: {
    data: {
      QID: String,
      Required: Boolean,
      Text: String,
      Holder: String,
    },
    change: Function,
    index: Number,
  },
  data: function () {
    return {
      editable: false,
    };
  },
  methods: {
    editing(value){
      this.$emit('isEditting', value)
    },
    remove() {
      this.$emit("remove", this.index);
    },
  },
  components: {
    toolbax,
  },
};
</script>