<template>
  <div id="radio">
    <div :class="editable ? 'hold' : ''" class="pt-2 pb-1">
      <div v-if="editable" class="px-3">
        <v-text-field
          class="font-weight-bold"
          v-model="data.Text"
          v-on:keyup.enter="editable = !editable"
          dense
          @focus="focusable"
        ></v-text-field>
        <div v-for="(item, i) in data.Option" :key="i">
          <v-text-field
            prepend-icon="mdi-radiobox-blank"
            class="my-2"
            v-model="data.Option[i].Text"
            v-on:keyup.enter="editable = !editable"
            dense
            @focus="focusable"
          >
            <template slot="append-outer">
              <!--v-if="i === data.Option.length - 1";:color="(added)?'primary':'grey'"-->
              <v-btn
                @click="
                  change();
                  addChild(i);
                  getQID();
                  added = !added;
                "
                color="success"
                text
                :key="i"
                >Child</v-btn
              >
              <v-icon
                @click="
                  change();
                  data.Option.splice(i, 1);
                "
                >mdi-delete</v-icon
              >
            </template>
          </v-text-field>
          <AddChild :index="i" :data="item" :change="change" :getQID="getQID"></AddChild>
        </div>
        <v-flex class="mx-3">
          <v-row>
            <v-col style="height: 40px" class="drag pa-0" cols="2">
              <v-btn
              elevation="0"
                class="ma-0"
                color="success"
                v-on:click="
                  data.Option.push({
                    Text: 'Ty chn ' + (data.Option.length + 1),
                  })
                "
                >+ Thm</v-btn
              >
            </v-col>
            <v-spacer></v-spacer>
            <toolbax
              :remove="remove"
              :change="change"
              @editable="editing(false);editable = $event;"
            ></toolbax>
          </v-row>
        </v-flex>
      </div>

      <div v-if="!editable" @click="editable = !editable; editing(true)">
        <span class="my-3 font-weight-bold">{{ data.Text }}</span>
        <v-radio-group class="mt-0 pt-2" :mandatory="false">
          <div v-for="(item, i) in data.Option" :key="i">
            <v-radio :value="i" class="handle my-1" :label="item.Text" />
          </div>
          <draggable
            v-model="data.Option"
            element="span"
            :group="{ name: 'question' }"
            :sort="false"
            handle=".handle"
            v-bind="dragOptions"
          >
            <!-- <transition-group type="transition" class="py-0" group tag="div">
            <div v-for="(item,i) in data.Option" :key="i">
              <v-radio :value="i" class="handle my-1" :label="item.Text" />
            </div>
          </transition-group> -->
          </draggable>
        </v-radio-group>
      </div>
    </div>
  </div>
</template>

<script>
import draggable from "vuedraggable";
import AddChild from "./child/AddChild";
import toolbax from "./layout/bottom-toolbar";

export default {
  name: "Radio",
  props: {
    change: Function,
    data: {
      QID: String,
      Text: String,
      Option: Array,
    },
    getQID: Function,
    index: Number,
  },
  data: function () {
    return {
      editable: false,
      added: false,
    };
  },
  methods: {
    editing(value){
      this.$emit('isEditting', value)
    },
    remove() {
      this.$emit("remove", this.index);
    },
    focusable(e) {
      e.target.select();
    },
    addChild(index) {
      this.data.Option = this.data.Option.map((option, idx) => {
        if (index === idx) {
          return {
            ...option,
            Des: "",
            Question: [],
          };
        }
        return option;
      });
    },
  },
  components: {
    draggable,
    AddChild,
    toolbax,
  },
  computed: {
    dragOptions() {
      return {
        animation: 200,
        disabled: false,
        ghostClass: "ghost",
      };
    },
  },
};
</script>
<style>
.v-messages {
  min-height: 0px !important;
}
</style>