<script>
import session from "./components/Session";
import formtitle from "./components/Formtitle";
import jsonviewer from "./components/dialog/JsonViewer";
import precommit from "./components/dialog/PreCommit";
import draggable from "vuedraggable";
import bottombar from "./components/layout/bottom-navigation";
import Sticky from "vue-sticky-directive";
import getdata from "./components/dialog/GetData";

export default {
  directives: {
    Sticky,
  },
  name: "App",

  data: function () {
    return {
      dragging: false,
      temp: [],
      items: ["Xa phn"],
      snackbar: {
        hover: false,
        snackbar: false,
        text: "",
        timeout: 5000,
      },
      nameTemplates: "untitled",

      templates: {
        templateID: "",
        logo_org:
          "https://lhu.edu.vn/ViewPage/LHUVN/_default/image/truong_dai_hoc_lac_hong_logo.png",
        name_org: "Trng i hc Lc Hng",
        url_finishRedirect: "https://lhu.edu.vn/",
        Title: "TN PHIU KHO ST",
        Des: "M t ca phiu kho st",
        url_login: "",
        url_template: "",
        Part: [
          {
            Name: "THNG TIN CHUNG",
            Des: "M t",
            Question: [],
          },
        ],
      },
      loadingBtn: false,
      oldTemplates: {},
    };
  },
  methods: {
    removeSession(i){
      console.log(i)
      this.change();
      this.templates.Part.splice(i, 1);
    },
    createNew: function () {
      if (
        confirm(
          "Mu kho st c s b xa, hy chc chn bn  lu mu trc khi to mi !"
        )
      ) {
        this.nameTemplates = "untitled";
        this.templates = {
          templateID: "",
          logo_org:
            "https://lhu.edu.vn/ViewPage/LHUVN/_default/image/truong_dai_hoc_lac_hong_logo.png",
          name_org: "Trng i hc Lc Hng",
          url_finishRedirect: "https://lhu.edu.vn/",
          Title: "TN PHIU KHO ST",
          Des: "M t ca phiu kho st",
          url_login: "",
          url_template: "",
          Part: [
            {
              Name: "THNG TIN CHUNG",
              Des: "M t",
              Question: [],
            },
          ],
        };
      }
    },
    change() {
      // this.temp.push({
      //   visible: true,
      //   model: JSON.parse(JSON.stringify(this.templates.Part))
      // });
      this.oldTemplates = JSON.parse(JSON.stringify(this.templates));
      this.snackbar.snackbar = true;
      // this.templates.Part = JSON.parse(JSON.stringify(this.temp[this.temp.length - 1].model));
    },
    // undo() {
    //   const a = [...this.temp].reverse().findIndex((i) => i.visible);
    //   this.temp[this.temp.length - 1 - a].visible = false;
    //   this.templates.Part = JSON.parse(JSON.stringify(this.temp[this.temp.length - 1 - a].model));
    // },
    undo: function () {
      this.templates = this.oldTemplates;
      this.snackbar.snackbar = false;
    },

    scrollToBottom() {
      let element = document.getElementById("endpoint");
      element.scrollIntoView({ behavior: "smooth", block: "end" });
    },
    handleSort() {
      this.templates.Part.map((value, index) => {
        const partID = ++index * 10000000;
        value.Question.map((e, i) => {
          const key = partID + ++i * 10000;

          if (e.Type === "MarkTable") {
            e.Topic.map((val, j) => {
              val.GroupID = key + ++j * 100;
              val.Question.map((ques, k) => {
                ques.QID = val.GroupID + ++k * 1;
              });
            });
            return;
          }
          if (e.Type === "Radio") {
            e.QID = key;
            e.Option.map((val) => {
              if (val.Question != undefined) {
                val.Question.map((ques, j) => {
                  ques.QID = e.QID + ++j * 1;
                });
              }
            });
            return;
          }
          if (e.Type === "Checkbox") {
            e.GroupID = key;
            e.Option.map((val, j) => {
              val.QID = e.GroupID + ++j * 10;
              if (val.Question != undefined) {
                val.Question.map((ques, k) => {
                  ques.QID = val.QID + ++k * 1;
                });
              }
            });
            return;
          }
          e.QID = key;
        });
      });
    },
    importTemplate(data) {
      this.templates = data;
    },
  },
  // updated() {
  //   this.scrollToBottom();
  // },
  // mounted() {
  //   this.scrollToBottom();
  // },
  components: {
    draggable,
    session,
    formtitle,
    jsonviewer,
    precommit,
    bottombar,
    getdata,
  },
  computed: {
    dragOptions() {
      return {
        animation: 100,
        disabled: false,
        ghostClass: "ghost",
      };
    },
    listString() {
      return JSON.stringify(this.templates, null, 2);
    },
  },
};
</script>

